<template>
  <div class="Button">
    <van-button
	:bind="data"
	:style="BtnStyle">{{data && data.value}}</van-button>
  </div>
</template>

<script>
export default {
  name: 'Button',
  props: {
    data: {
      type: [Object, String],
      defalut: () => {
        return {}
      }
    }
  },
  computed: {
    BtnStyle: function() {
      let style = this.data.style
      if(style) {
        try {
          style = JSON.parse(style)
          return style
        } catch(err) {
          return {}
        }
      } else {
        return {}
      }
    }
  },
  components: {
  },
  mounted() {
	  console.log('button111',this.data)
  }
}
</script>

